import React, { Component } from 'react'

export const DataContext = React.createContext();
export class DataProvider extends Component {
    state = {
        products: [
            {
                "_id": "1",
                "title": "Nike Shoes 01",
                "src": "https://www.upsieutoc.com/images/2020/06/27/img1.jpg",
                "description": "UI/UX designing, html css tutorials",
                "content": "Welcome to our channel Dev AT. Here you can learn web designing, UI/UX designing, html css tutorials, css animations and css effects, javascript and jquery tutorials and related so on.",
                "price": 23,
                "colors":["red","black","crimson","teal"],
                "count": 1
            },
            {
                "_id": "2",
                "title": "Nike Shoes 02",
                "src": "https://www.upsieutoc.com/images/2020/06/27/img2.jpg",
                "description": "UI/UX designing, html css tutorials",
                "content": "Welcome to our channel Dev AT. Here you can learn web designing, UI/UX designing, html css tutorials, css animations and css effects, javascript and jquery tutorials and related so on.",
                "price": 19,
                "colors":["red","crimson","teal"],
                "count": 1
            },
            {
                "_id": "3",
                "title": "Nike Shoes 03",
                "src": "https://www.upsieutoc.com/images/2020/06/27/img3.jpg",
                "description": "UI/UX designing, html css tutorials",
                "content": "Welcome to our channel Dev AT. Here you can learn web designing, UI/UX designing, html css tutorials, css animations and css effects, javascript and jquery tutorials and related so on.",
                "price": 50,
                "colors":["lightblue","white","crimson","teal"],
                "count": 1
            },
            {
                "_id": "4",
                "title": "Nike Shoes 04",
                "src": "https://www.upsieutoc.com/images/2020/06/27/img4.jpg",
                "description": "UI/UX designing, html css tutorials",
                "content": "Welcome to our channel Dev AT. Here you can learn web designing, UI/UX designing, html css tutorials, css animations and css effects, javascript and jquery tutorials and related so on.",
                "price": 15,
                "colors":["orange","black","crimson","teal"],
                "count": 1
            },
            {
                "_id": "5",
                "title": "Nike Shoes 05",
                "src": "https://www.upsieutoc.com/images/2020/06/27/img5.jpg",
                "description": "UI/UX designing, html css tutorials",
                "content": "Welcome to our channel Dev AT. Here you can learn web designing, UI/UX designing, html css tutorials, css animations and css effects, javascript and jquery tutorials and related so on.",
                "price": 10,
                "colors":["orange","black","crimson","teal"],
                "count": 1
            },
            {
                "_id": "6",
                "title": "Nike Shoes 06",
                "src": "https://www.upsieutoc.com/images/2020/06/27/img6.jpg",
                "description": "UI/UX designing, html css tutorials",
                "content": "Welcome to our channel Dev AT. Here you can learn web designing, UI/UX designing, html css tutorials, css animations and css effects, javascript and jquery tutorials and related so on.",
                "price": 17,
                "colors":["orange","black","crimson","teal"],
                "count": 1
            }
        ],
        cart :[

        ],
        totalBill : 0

    }
    addCart = (id)=>{
        const {products , cart} = this.state;
        const check = cart.every(item =>{
            return item._id !== id
        })
        if(check){
            const data = products.filter((product, index)=>{
                return product._id === id
            })
            this.setState({
                cart: [...cart , ...data]
            })
        }else{
            alert("Product has been added to Cart")
        }

    };

    reduction = (id)=>{
        const {cart} = this.state;
        cart.forEach((item)=>{
            if(item._id === id){
                item.count === 1 ? item.count =1 : item.count -=1;
            }
        })
        this.setState({
            cart: cart 
        })
        this.getBill();

    };
    increasion = (id)=>{
        const {cart} = this.state;
        cart.forEach((item)=>{
            if(item._id === id){
                item.count +=1;
            }
        })
        this.setState({
            cart: cart 
        })
        this.getBill();
    };
    getBill = ()=>{
        const {cart} = this.state;
        const res = cart.reduce((total , item)=>{
            return total + item.price * item.count;
        }, 0)
        this.setState({totalBill: res})
    };
    deleteitem = (id)=>{
        if(window.confirm("Are you sure want to delete this ?")){
            const {cart} = this.state;
            cart.forEach((item,index) =>{
                if(item._id === id){
                    cart.splice(index, 1)
                }
            })
            this.setState({cart: cart})
            this.getBill();
        }

    };
    componentDidUpdate(){
        localStorage.setItem('dataCart' , JSON.stringify(this.state.cart));
        localStorage.setItem('dataTotal', JSON.stringify(this.state.totalBill))
    }
    componentDidMount(){
        const dataCart = JSON.parse(localStorage.getItem('dataCart'));
        if(dataCart !== null){
            this.setState({cart: dataCart})
        }
        const totalBill = JSON.parse(localStorage.getItem('dataTotal'));
        if(totalBill !== null){
            this.setState({totalBill: totalBill})
        }
    }
    render() {
        const {products,cart,totalBill} = this.state;
        const {addCart, reduction ,getBill, increasion ,deleteitem} = this
        return (
            <DataContext.Provider value = {{products,getBill,cart,totalBill,reduction,increasion,deleteitem, addCart}}>
                {this.props.children}
            </DataContext.Provider>
        )
    }
}

